<template>
  <div class="honm">
    <van-nav-bar>
    <template #right>
        <div class="bt">
          <div class="times" v-if="grt=(ho<9?'早上好':ho<12?'上午好':ho<15?'中午好':ho<19?'下午好':'晚上好')">{{grt}}</div>
          <div class="names">sirl</div>
        </div>
      </template>
      <template #left>
        <van-search  v-model="keywords" @focus="focus" placeholder="请输入搜索关键词" shape="round" />
          <div class="cross" @click="dj">X</div>
      </template>
        
    </van-nav-bar>
    <div class="dl">
      <div class="ls"  @click="visd(item.pid)" v-for="(item,index) in sousuo1()" :key="index">
      <div class="stp">
        <img :src="item.largeImg">
      </div>
      <div class="smz">{{item.name}}</div>
      <div class="syw">{{item.enname}}</div>
      <div class="sprice">{{item.price}}</div>
    </div>
    </div>
    

    <van-swipe class="my-swipe" @change="changeCurrentIndex" autoplay='3000'>
      <van-swipe-item v-for="(item,index) in baeers" :key="index">
          <img @click="viewProduct(item.pid)" class="bar" :src="item.bannerImg"/>
      </van-swipe-item>

        <template #indicator>
            <div class="hk">
                <div class="custom-indicator" :class="{active: currentIndex === i}" v-for="(v,i) in baeers.length" :key="i"></div>
            </div>
        </template>
    </van-swipe>

    <div class="hottuijian">
        <div class="tuijian">
          <div class="ms">
            <img src="../assets/img/remai1.1.jpg" alt="">
          </div>
        </div>
            <div class="sptj">
              <transition-group name="ns" enter-active-class="animated bounceInRight">
                <Hottj class="spimg" v-for="(item,index) in hotsps" :key="index" :product='item'></Hottj>
              </transition-group>
            </div>
    </div>
  </div>
</template>
<script>
import Hottj from '../components/Hottj'

export default {
  name: "Honm",
  data() {
    return {
        currentIndex:0,

        //轮播图数据
        baeers:[],

        //热卖商品
        hotsps:[],

        grt:"hai",

        ho:new Date().getHours(),

        keywords:'',

        sera:[],
    };
  },
  components: {
    Hottj  
  },
    created () {
        this.baeer();

        this.hotsp();

        this.shousuo();
    },

    computed: {
      Search(){
          if(this.keywords){
              return this.sera.filter((value)=>{
                  return value.name.includes(this.keywords)
              })
          }
           
      },
  },

  methods: {
      changeCurrentIndex(index) {
    //   
      this.currentIndex = index;
    },
    //轮播图数据获取
    baeer() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/banner",
        params:{
            appkey:this.appkey
        }
      }).then((res) => {
          
          this.$toast.clear();
        
           if (res.data.code == 300) {
             this.baeers = res.data.result
           } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
           }
        }).catch((err) => {
          this.$toast.clear();
          
        });
      },

      //热卖商品获取
      hotsp() {
        this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
        });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/typeProducts",
        params:{
            appkey:this.appkey,
            key:'isHot',
            value:1
        }
      }).then((res) => {
          
          this.$toast.clear();
        
           if (res.data.code == 500) {
             this.hotsps = res.data.result
           } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
           }
        }).catch((err) => {
          this.$toast.clear();
          
        });
      },

      viewProduct(pid) {
      this.$router.push({name: 'Detail', params: {pid}});
    },
    
      //  搜索
    shousuo(keywords) {
        // 
        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/search",
        params: {
          appkey: this.appkey,
          name:this.keywords
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == "Q001") {
            this.sera = res.data.result;

          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    sousuo1(){
        if(!this.keywords){
            return this.sera;
            
        }
        return this.Search;
        
    },

    visd(pid) {
      this.$router.push({name: 'Detail', params: {pid}});
    },

    focus(){
          let dl = document.querySelector(".dl")
          dl.style.display = "block"
  
    },

    dj(){
       let dl = document.querySelector(".dl")
          dl.style.display = "none"
    }

    },
};
</script>

<style lang="less" scoped>
.honm {
  position: relative;
  /deep/.van-icon-search::before {
    content: "\F0AF";
    color: #ffff32;
  }
  .cross{
    width: 12px;
    height: 12px;
    background: gray;
    border: 1px solid gray;
    color: white;
    border-radius: 50%;
    position: absolute ;
    font-size: 8px;
    line-height: 12px;
    top: 16px;
    left: 239px;
    z-index: 5;
  }
  .bt {
    .times {
      float: left;
      font-size: 18px;
      font-weight: bold;
    }
    .names {
      float: left;
      font-size: 18px;
      color: blue;
      margin-left: 15px;
    }
  }
  .my-swipe{
      border-radius: 15px;
      margin: 10px;
      position: relative;
      .bar{
        width: 100%;
      }
      .hk{
          position: absolute;
          right: 15px;
          bottom: 10px;  
          display: flex;
          .custom-indicator{
              width: 10px;
              height: 2px;
              background: white;
              margin-left: 5px;
              transform: all 0.3s linear;
          }
          .custom-indicator.active{
              background: skyblue;
              width: 16px;
          }
      }
  }
  .hottuijian{
     width: 100%;
      .tuijian{
          width: 95%;
          margin-left: 10px;
          background: linear-gradient(to right, #f6c73d 0%, pink 100%);
          border-radius: 15px;
          .ms{
              width: 100px;
              height: 100px;
              margin-top: 2px;
              margin-left: 135px;
              img{
                width: 100%;
                height: 100%;
                border-radius: 50%;
          }
           }
           
      }
      .sptj{
          margin-top: 15px;
          .spimg{
              margin-top: 15px;
          }
      }
  }
  .dl{
    display: none;
    position: absolute;
    top: 55px;
    left: 9px;
    z-index: 5;
    width: 95%;
    // height: 600px;
    // background: rgba(250,250,250,0.6);
    
    .ls{
      width: 118px;
      height: 118px;
      float: left;
      background: rgba(250,250,250,1);
    .stp{
      margin-left: 35px;
      margin-top: 15px;
      width: 45px;
      height: 45px;
      img{
        width: 45px;
        height: 45px;
      }
    }
    .smz{
      margin-left: 35px;
    }
    .syw{
      width: 75px;
      height: 15px;
      white-space:nowrap;
      overflow: hidden;
      margin-left: 35px;

    }
    .sprice{
      margin-left: 35px;
      color: red;
    }
  }
  }
  
}
</style>